.video {
    position: relative;
    background-color: black;
    height: 75vh;
    min-height: 25rem;
    width: 100%;
    overflow: hidden;
}

.video video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: 0;
    -ms-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
}

.video .container {
    position: relative;
    z-index: 2;
}

.video .overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    /*background-color: black;*/
    /*opacity: 0.5;*/
    z-index: 1;
}

.video-content {
    color:white;
    /*border-bottom: 10px solid black;*/
    /*padding-bottom: 10px;*/
    padding:20px;
    text-align: center;
    text-transform: uppercase;
    @media (max-width: 576px) {
        text-align:center;
    }
}

@media (pointer: coarse) and (hover: none) {
    .video {
        background: url('http://demo.studiospica.pl/wp-content/uploads/2021/03/5G-Montage-Berlin-19-1.jpg') black no-repeat center center scroll;
    }
    .video video {
        display: none;
    }
}