// Your variable overrides
// Colors
$red:           #db261e;
$brown:         #996633;
$white:         #ffffff;
$light-gray:    #f9f9f9;
$dark-gray:     #eeeeee;

// Theme default colors
$primary:   $white !default;
$secondary: $red !default;

// Colors for caspian
$orange:        #d8a91b;
$brown:         #934228;

// Theme default colors
$primary:   $white !default;
$secondary: $red !default;

$body-color:    #000;

// Font defaults
$font-family-base:  'Asap', sans-serif;
$font-size-base:    1rem !default;

$font-size-sm:      12px !default;
$font-size-md:      1.125rem !default;
$font-size-l:       2.125rem !default;
$font-size-xl:      3.125rem !default;
$font-size-price-s: 1.5rem !default;
$h1-font-size:      $font-size-l;

$font-weight-lighter:         200 !default;
$font-weight-light:           300 !default;
$font-weight-normal:          400 !default;
$font-weight-medium:          500 !default;
$font-weight-semibold:        600 !default;
$font-weight-bold:            700 !default;
$font-weight-bolder:          800 !default;

// Headings
$headings-margin-bottom:      1rem;
$headings-font-weight:        $font-weight-semibold;

// Links
$link-decoration:   none;

// Buttons + Forms
$input-btn-padding-y:   .375rem !default;
$input-btn-padding-x:   1rem !default;
$btn-font-size:         .875rem;

// Modal
$modal-content-bg:  $light-gray;

// Klaro
$cm-font-color-dark: #fff;
$cm-border-radius: 0;
$cm-blue1: $primary;
$cm-green1: $primary;

// Border

$border-black: 10px solid black;