.footer-container {
    background: $light-gray;
}
.widget-area {
    display:flex!important;
    text-transform: uppercase;
}
.widget-item {
    padding: 10px 50px 0px 0px;
    font-weight: 600;
    font-size: $font-size-sm;
    ul {
        list-style: none;
        padding-left: 0.1rem;
    }
    h2 {
        font-size: $font-size-md;
    }
    a {
        color: $body-color;
    }
}
.widget-sidebar {
    display: flex;
    justify-content: flex-start;
    min-height: 200px;
    align-items: center;
}

.footer-logo {
    text-align: end;
    @media (max-width:1190px) {
        text-align:center;
        padding-bottom: 50px;
    }
}

@media (max-width: 1190px) {
    nav {
        display:flex;
    }
    .navbar {
        text-align: center;
    }
    .widget-area {
        margin-top:1rem;
        margin-bottom:1rem;
        text-align: center;
        display:flex!important;
        flex-direction: column;
    }
    .nav-item {
        background: $light-gray;
        border-bottom: 1px solid $dark-gray;
    }
    .widget-item {
        padding: 5px 0px 0px 5px;
        font-size:$font-size-sm;
    }
}