.header-container {
    min-height: 100px;
    background-color: #fff;
}
.header-subtitle {
 font-size:$font-size-sm;
 font-weight:$font-weight-bold;
}
.top-header {
    .widget-item {
        padding:10px 0 0 0;
        a {
            color: white; 
        }
    }
}

.site-logo {
    padding: 20px 0;

    & img {
        height: 40px;
        width: auto;
    }
}

.navbar { 

    .navbar-nav {

        .nav-item {
            padding: 0px;
            margin-left: 12px!important;
            /*margin-right: 12px;*/
            min-height: 10px!important;
            /*border-bottom: 4px solid black;*/
            /*border-left: 5px solid black;*/
            &.active {
                a{
                    color: $red;

                }

                @media (max-width: 1199px) {
                    /*border-bottom: 1px solid #eeeeee;*/

                    a {
                        text-decoration: underline #934228;
                        text-decoration-thickness: 4px;
                        text-underline-offset: 3px;
                    }
                }
            }

            a {
                padding-bottom: 0.3rem;
                color: $body-color;
                font-size: $font-size-sm;
                font-weight: $font-weight-semibold;
                text-decoration: none;
                text-transform: uppercase;

                &:hover {
                    color: $secondary;
                    transition: 200ms;
                }
            }

            @media (max-width: 1199px) {
                font-size: $font-size-sm!important;
                font-weight: $font-weight-semibold!important;
                padding-bottom: 5px!important;
                padding-top: 5px!important;
                margin-left: 0px!important;
                margin-right: 0px!important;
                width: 100%;
            }
        }
    }
}

@media (max-width: 1199px) {
    .header-widget-area {
        display:none;
    }
    .navbar-toggler:focus {
        box-shadow: unset!important;
    }
    .custom-toggler .navbar-toggler-icon {
        box-shadow: unset!important;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0,0,0, 0.5)' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
    }
    .custom-toggler:active .navbar-toggler-icon:active {
        box-shadow: unset!important;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(147,66,40, 0.5)' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
    }

    .header-nav-area {
        padding: 0px!important;

        .row {
            padding: 0px!important;
        }

        .navbar {
            padding: 0px!important;
        }
    }
}