.video-container {
    border-bottom: $border-black;
}
.video-content {
    @media (max-width: 576px) {
        align-self: center;
    }
}

.about-container {
    border-bottom: $border-black;
    h2 {
        text-transform: uppercase;
    }
    p {
        font-size: $font-size-sm;
        text-align: justify;
    }
    a {
        color: black;
        font-size: $font-size-sm;
        font-weight: $font-weight-bold;
    }
}
.brand-container {
    @media (max-width: 576px) {
        flex-direction: column;
    }
}

.img-thumbnail {
    border: none;
    @media (max-width: 576px) {
        padding:20px;
    }
}
.news-container {
    h2 {
        text-transform: uppercase;
    }
    @media (max-width: 576px) {
        flex-direction: column;
    }
    .news-card {
        border-radius:0px;
        border: none;
        border-bottom: $border-black;
        min-height: 400px;
        /*background-size: contain;*/
        font-size: $font-size-sm;
        @media (max-width: 576px) {
            min-height: unset;
        }
        .card-body {
            background:rgba(0,0,0,0.3);
        }
        h2 {
            color:$primary; 
            text-align: center;
        }
        p {
            color:$primary;
            text-align: justify;
        }
        .btn {
            align-self: center;
        }
    }

    .news-list {
        font-size: $font-size-sm;
        ul {
            list-style: none;
            padding-left: 0px;
            li {
                a {
                    color:black;
                    font-weight: $font-weight-bold;
                }
                p {
                    margin-bottom: 5px;
                }
            } 
        }
    }
}
